//
//  Foundation
//
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;

  font-family: 'Poppins', sans-serif;

  perspective: 1000px;
}



//
//  Layout
//
.container {
  display: flex;
  justify-content: center;
  align-items: center;
}




//
//  Card
//
.card {
  width: 35rem;
  min-height: 80vh;
  padding: 0rem 5rem;

  border-radius: 30px;
  box-shadow: 0 20px 20px rgba(0, 0, 0, 0.2), 0px 0px 50px rgba(0, 0, 0, 0.2);

  transform-style: preserve-3d;
}



.sneaker {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 35vh;


  img {
    width: 20rem;
    z-index: 2;
    transition: all 0.75s ease-out;
  }
}



.circle {
  position: absolute;
  z-index: 1;

  width: 15rem;
  height: 15rem;

  background: linear-gradient(
    to right,
    rgba(245, 70, 66, 0.75),
    rgba(8, 83, 156, 0.75)
  );
  border-radius: 50%;
}



.info {
  //...


  h1 {
    font-size: 3rem;
    transition: all 0.75s ease-out;
  }
  h3 {
    font-size: 1.3rem;
    padding: 2rem 0rem;
    color: #585858;
    font-weight: lighter;
    transition: all 0.75s ease-out;
  }
}



.sizes {
  display: flex;
  justify-content: space-between;

  transition: all 0.75s ease-out;


  button {
    padding: 0.5rem 2rem;

    font-weight: bold;
    color: #585858;

    background: none;
    border: none;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
    border-radius: 30px;

    cursor: pointer;

    &:focus,
    &:hover {
      background: lighten(#585858, 50%);
    }

    &.active {
      background: #585858;
      color: white;
    }
  }
}




.purchase {
  margin-top: 3rem;
  margin-bottom: 2rem;

  transition: all 0.75s ease-out;

  button {
    width: 100%;
    padding: 1rem 0rem;

    font-weight: bolder;
    color: white;

    background: #f54642;
    border: none;
    border-radius: 30px;

    cursor: pointer;

    &:focus,
    &:hover {
      background: darken(#f54642, 20%);
    }
  }
}
